.container {
  max-width: 940px; }

*, *::before, *::after {
  box-sizing: border-box; }

html {
  height: 100%;
  scroll-behavior: smooth; }

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: Lato, sans-serif; }

h1, h2, h3 {
  margin-top: 0;
  margin-bottom: 0; }

.wrapper {
  display: flex;
  flex-direction: column;
  flex-grow: 1; }

.logo {
  color: #5f5f5f;
  font-size: 1.875rem;
  text-decoration: none;
  text-transform: uppercase; }

.phone {
  display: flex;
  flex-direction: column;
  align-items: flex-end; }
  .phone__number {
    font-size: 1.75rem;
    font-weight: 700;
    text-decoration: none;
    transition: color 0.3s linear; }
    .phone__number--light {
      color: #ffffff; }
      .phone__number--light:hover {
        color: #bfbfbf; }
    .phone__number--dark {
      color: #000000; }
      .phone__number--dark:hover {
        color: #737373; }
  .phone__callback {
    color: #d91233;
    text-decoration: underline;
    transition: color 0.3s linear; }
    .phone__callback:hover {
      color: #f68e9f; }

.img {
  width: 100%; }

.picture {
  display: flex;
  justify-content: center;
  align-items: center; }

.col__picture {
  flex: 1 0 auto; }

.col__description {
  font-size: 1.125rem;
  text-align: center;
  margin-top: 1.5em; }

.col__subtitle {
  margin-top: 1.875em;
  font-size: 1rem;
  font-weight: 700; }

.col__text {
  font-size: 0.875rem;
  text-align: center;
  margin-top: 1.428em;
  margin-bottom: 0; }

.button {
  display: block;
  width: 15rem;
  padding-top: 0.723em;
  padding-bottom: 0.723em;
  font-size: 1.125rem;
  font-weight: 700;
  border-radius: 1.563rem;
  border: 0.125rem solid #d91233;
  background-color: #d91233;
  color: inherit;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s linear; }
  .button:hover {
    background-color: transparent; }

.section {
  padding-top: 5rem;
  padding-bottom: 5rem; }

.title {
  font-size: 1.875rem; }
  .title--center {
    text-align: center; }

.text {
  margin-top: 1.25em;
  margin-bottom: 3.75em; }
  .text--center {
    text-align: center; }

.form {
  display: flex;
  justify-content: space-between;
  border: 0.313rem solid #ffffff;
  padding: 3.75rem; }
  .form__col {
    flex-basis: 15rem; }
  .form__item {
    display: flex;
    flex-direction: column;
    margin-top: 1.25rem; }
  .form__button {
    color: #ffffff;
    margin-top: 1.875rem; }
    .form__button:hover {
      color: #000000; }

.label {
  display: flex;
  font-size: 0.813rem;
  margin-bottom: 0.615em; }

.select {
  width: 100%;
  padding: 0.923em 3.461em 0.923em 1.307em;
  background: #ffffff url("../img/request/icon/arrow.svg") top 0.769em right 1.153em no-repeat;
  appearance: none;
  font-size: 0.813rem;
  border: none;
  cursor: pointer; }

.input {
  width: 100%;
  background-color: #ffffff;
  padding: 0.923em 3.461em 0.923em 1.307em;
  font-size: 0.813rem;
  border: none; }

.textarea {
  font-size: 0.813rem;
  border: none;
  width: 100%;
  max-height: 7.5rem;
  padding: 1.076em 1.307em;
  resize: none; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.hero {
  position: relative;
  min-height: 100vh;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
  background: url("../img/hero/background/hero-bg.jpg") center/cover no-repeat;
  color: #ffffff;
  display: flex; }
  .hero::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: '';
    background-color: rgba(33, 33, 33, 0.85); }
  .hero__container {
    position: relative;
    display: flex;
    flex-direction: column; }
  .hero__content {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex-grow: 1; }
  .hero__title {
    font-family: "Lato Bold";
    font-size: 3rem;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center; }
  .hero__subtitle {
    display: block;
    font-size: 1.875rem;
    font-weight: 400;
    text-transform: none; }
  .hero__col {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .hero__text {
    max-width: 10rem; }

.about {
  padding-top: 5rem;
  padding-bottom: 0.375rem; }
  .about__col {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    margin-bottom: 5rem; }

.request {
  background-color: #ebebeb; }

.cars {
  padding-top: 5rem;
  padding-bottom: 1.25rem; }
  .cars__row {
    margin-top: 3.75rem;
    opacity: 0.6; }
  .cars__picture {
    margin-bottom: 1.25rem; }
    .cars__picture:first-of-type {
      margin-left: 6.38%; }
    .cars__picture:nth-of-type(5) {
      margin-right: 6.38%; }
    .cars__picture:nth-of-type(4n+6) {
      margin-left: 12.76%; }
    .cars__picture:nth-of-type(4n+9) {
      margin-right: 12.76%; }

.video {
  padding-top: 1.25rem;
  padding-bottom: 5rem; }
  .video__wrap {
    max-width: 51.25rem;
    padding: 1.25rem;
    border: 0.313rem solid #ebebeb;
    margin-top: 3.74rem;
    margin-left: auto;
    margin-right: auto; }
  .video__content {
    background-color: #000000;
    height: 24.375rem; }

.clients {
  background-color: #ebebeb;
  padding-bottom: 1.5625rem; }
  .clients__row {
    margin-top: 3.75rem; }
  .clients__item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 3.437rem; }
  .clients__picture {
    max-width: 5.625rem;
    max-height: 5.563rem;
    overflow: hidden;
    border-radius: 50%; }
  .clients__content {
    flex-basis: 20.375rem; }
  .clients__author {
    display: block;
    color: #787878;
    font-size: 0.75rem;
    font-weight: 700; }
  .clients__subtitle {
    margin-top: 0.75em;
    color: #d91233; }
  .clients__text {
    margin: 1.077em 0 0;
    color: #2c2c2c;
    font-size: 0.813rem;
    font-weight: 700;
    text-align: left; }

.footer {
  padding-top: 1.875rem;
  padding-bottom: 1.875rem; }
  .footer__container {
    display: flex;
    justify-content: space-between;
    align-items: center; }
